@import "./SASS/const";

*,
::after,
::before {
  box-sizing: border-box;
}

.p-1 {
  padding: 0 1rem !important;
}

.containerCust {
  max-width: 1040px;
  padding: 40px;
  margin: auto;
}

.input-field input[type="text"]:focus + label {
  color: #90caf9;
}
.input-field input[type="text"]:focus {
  border-bottom: 1px solid #90caf9;
  box-shadow: 0 1px 0 0 #90caf9;
}

.input-field .prefix.active {
  color: #90caf9;
}

.input-field input[type="text"].valid {
  border-bottom: 1px solid #90caf9;
  box-shadow: 0 1px 0 0 #90caf9;
}
/* invalid color */
.input-field input[type="text"].invalid {
  border-bottom: 1px solid #90caf9;
  box-shadow: 0 1px 0 0 #90caf9;
}

.card,
button.btn {
  border-radius: 12px;
}

.card .card-image img {
  border-radius: 12px 12px 0 0;
}

.input-field input {
  border-radius: 12px;
}

button.btn-non-style {
  background: transparent;
  border: none;
  padding: 0;
  line-height: 0;
  color: white;
  &:hover {
    cursor: pointer;
  }
}

.tabs .tab a {
  color: $dark;
}

h2 {
  font-size: 22px;
  text-align: center;
}

.animEl {
  animation: fadeInContent 0.4s;
}

.containerCust {
  animation: fadeInContentLight 0.4s;
}

@keyframes fadeInContent {
  from {
    margin-top: 2vh;
    filter: blur(2px);
    opacity: 0;
  }
}

@keyframes fadeInContentLight {
  from {
    filter: blur(2px);
    opacity: 0;
  }
}

@media all and (max-width: 600px) {
  h2 {
    font-size: 20px;
  }
}
